  //   content: '';
  //   display: block;
  //   width: 200px;
  //   height: 200px;
  //   background-image: url('../images/modal/ice-bg.png');
  //   background-repeat: no-repeat;
  //   background-size: contain;
  // }
}

#upbutton {
  bottom: 20px;
  right: 15px;
  cursor: pointer;
  display: none;
  position: fixed;
  z-index: 999;

  @media screen and (min-width: 768px) {
    bottom: 15px;
  }

  @media screen and (min-width: 1280px) {
    bottom: 45px;
    right: 20px;
  }
}

.rocket {
  display: block;
  position: absolute;
  top: 10%;
  left: 12%;
  width: 38px;
  height: 38px;
  fill: $accent-color;
}

.rocket-wrapper {
  display: block;
  position: relative;
  width: 52px;
  height: 52px;
  background-color: rgba(255, 165, 186, 0.2);
  border-radius: 50%;
}
